body {
  margin: 20px 25px;
}

@media(max-width: 490px) {
  body {
      margin: 0px;
  }
}

* {
  font-family: Arial, Helvetica, sans-serif
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

header, main, div, section, h1, h2, h3, h4, h5, h6 {
  margin: 0px;
  padding: 0px;
}
